<template>
  <div class="tabs-header-container">
    <div class="tabs-header">
      <div class="header-container">
        <div class="header">
          <svg-icon name="offers" width="48" height="48" />
          <h1 class="font-weight-semi">{{ $t('header.my_offers') }}</h1>
          <p>
            {{ $t('home.my_dashboard_details') }}
          </p>
        </div>
        <b-overlay rounded="sm" :opacity="1">
          <div class="rent-tabs">
            <b-tabs class="tabs-container container" align="center" justified>
              <b-tab
                :title="dir === 'ltr' ? 'Sent offers' : 'العروض المرسلة'"
                :active="$route.query.type === 'public'"
                @click="switchTab('public')"></b-tab>
              <b-tab
                :title="dir === 'ltr' ? 'Incoming offers' : 'الطلبات المباشرة'"
                :active="$route.query.type === 'private'"
                @click="switchTab('private')"></b-tab>
            </b-tabs>
          </div>
        </b-overlay>
      </div>
    </div>
    <b-overlay :show="loading" rounded="sm" :opacity="1">
      <b-container>
        <b-row v-if="haveOffers" class="justify-content-center">
          <b-col cols="12">
            <div class="filters mt-3">
              <div class="filters_wrapper d-flex justify-content-between align-items-center">
                <div class="req_filters">
                  <chip-selector :options="req_filters" @update-selection="updateFilters"></chip-selector>
                </div>
                <b-button class="bg-white d-flex align-items-center">
                  <img class="mx-1" width="20px" height="20px" src="@/assets/images/home/filters.svg" alt="Filters">
                  Filter
                </b-button>
              </div>
            </div>
          </b-col>
          <b-col
            xl="4"
            lg="6"
            cols="12"
            v-for="(offer, index) in offers"
            :key="index">
            <NuxtLink
              class="text-dark"
              :to="localePath(`/dashboard/offers/${offer.id}`)"
              v-if="$route.query.type === 'public'">
              <LandlordOfferCard_2 :item="offer" />
            </NuxtLink>
            <LandlordPrivateRequestCard v-else :item="offer" />
          </b-col>
        </b-row>
        <div class="no-offers" v-else>
          <svg-icon name="smiley-meh" width="48" height="48"></svg-icon>
          <h3>You have not sent any offers yet.</h3>
        </div>
        <div class="mt-3" v-if="haveOffers && pagination">
          <b-pagination
            :value="pagination.current_page"
            :total-rows="pagination.total"
            :per-page="pagination.per_page"
            @input="handlePagination"
            pills
            size="md"
            align="center"></b-pagination>
        </div>
      </b-container>
    </b-overlay>
  </div>
</template>

<script>
import LandlordOfferCard_2 from '@/components/dashboard/LandlordOfferCard_2';
import LandlordPrivateRequestCard from '@/components/dashboard/LandlordPrivateRequestCard';
export default {
  components: {
    LandlordOfferCard_2,
    LandlordPrivateRequestCard,
    ChipSelector: () => import('~/components/shared/form/chipSelector'),
  },
  data() {
    return {
      offers: [],
      filterReq: '',
      loading: false,
      haveOffers: true,
      pagination: {},
      req_filters: [{label: 'All', value: 'all'}, {label: 'Waiting Your Response', value: 'respond'}],
      isRadio: true,
    };
  },
  head() {
    return {
      title: `${this.dir === 'ltr' ? 'My Offers' : 'عروضى'} ${
        this.appEnv !== 'production' ? '| Staging' : ''
      }`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === 'ltr' ? 'My Offers' : 'عروضى',
        },
      ],
    };
  },
  watch: {
    '$route.query': '$fetch',
  },
  methods: {
    async getSentOffers() {
      this.loading = true;

      const { Data } =
        this.$route.query.type === 'private'
          ? await this.$axios.$get(`/user/me/received-requests`)
          : await this.$axios.$get(`/user/me/sent-offers`);

      this.offers = Data.collection;
      this.pagination = Data.pagination;

      if (this.offers && this.offers.length === 0) this.haveOffers = false;
      this.loading = false;
    },
    switchTab(tab) {
      this.$router.push({ query: { type: tab } });
    },
    handlePagination(page) {
      this.$router.push({ query: { ...this.$route.query, page } });
    },
    updateFilters(val){
      // if(val.length > 1){
      //   this.filterReq = val[1];
      // }else{
      //   this.filterReq = val[0];
      // }
      console.log(val);
    }
  },
  computed: {
    appEnv() {
      return this.$config.APP_ENV;
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
  },
  async fetch() {
    await this.getSentOffers();
  },
};
</script>

<style lang="scss">
.tabs-header-container {
  background-color: #f7f9fa;
  position: relative;
  top: -25px;
  min-height: 100vh;

  @media screen and (max-width: 992px) {
    top: -45px;
  }

  .tabs-header {
    background-color: #fff;
    margin: 0px -15px;
    padding: 20px 25px 0px;
    box-shadow: 0px 0px 24px 0px rgba(0, 0, 0, 0.26);
    -webkit-box-shadow: 0px 0px 24px 0px rgba(0, 0, 0, 0.26);
    -moz-box-shadow: 0px 0px 24px 0px rgba(0, 0, 0, 0.26);

    .header-container {
      .header {
        padding: 25px;
        text-align: center;

        h1 {
          text-align: center;
          margin: 10px 0px;
          color: #000;
        }

        p {
          margin-bottom: 15px;
          text-align: center;
          color: #48535b;
        }
      }
    }
  }

  .filters {
    .btn {
      height: 35px;
    }
  }

  .no-offers {
    text-align: center;
    margin: 64px 0;

    h3 {
      text-align: center;
      font-weight: bold;
      font-size: 18px;
      margin-top: 16px;
    }
  }
}

.rent-tabs {
  padding: 15px;
  border-top: solid 1px #f2f4f5;

  .tabs-container {
    background-color: #f2f4f5;
    border-radius: 6px;
    padding: 4px;

    ul {
      border: none;

      .nav-item {
        a {
          color: #48535b;
          font-weight: bold;
          text-align: center;
        }

        .active {
          background-color: #0078ff !important;
          color: #fff;
          border: none;
          border-radius: 6px;
        }
      }
    }
  }
}
</style>
