<template>
  <div class="rejection-model">
    <b-modal id="modal-center" centered v-model="toggleModel">
      <template #modal-header>
        <svg-icon name="negotiation" width="48" height="48" />
        <h3>Send Your Offer</h3>
      </template>
      <div class="form-group">
        <div class="form-input">
          <h5 class="font-size-normal">
            {{ $t('form.postingRequest.stayingDetails.budget_range') }}
          </h5>
          <template>
            <AppInput
              :placeholder="
                $t('form.postingRequest.stayingDetails.budget_placeHolder')
              "
              v-model="form.price"
              type="number"></AppInput>
          </template>
        </div>
        <div class="form-input" v-if="form.move_in_date">
          <h5 class="font-size-normal">
            {{ $t('form.postingRequest.stayingDetails.starting_date') }}
          </h5>
          <b-form-datepicker
            :date-format-options="dateFormatOptions"
            id="datepicker"
            v-model="form.move_in_date"
            @input="changeDate"
            :placeholder="$t('common.date')"
            today-button
            close-button
            locale="en"
            :min="new Date().toJSON().slice(0, 10).replace(/-/g, '-')"
            aria-describedby="inputcheck-in-feedback" />
        </div>
      </div>
      <template #modal-footer>
        <div class="actions">
          <b-button @click="$emit('closeSendOfferModel')"> Cancel </b-button>
          <b-button
            variant="primary"
            @click="$emit('sendOffer', form)"
            :disabled="disabled">
            Send
          </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
export default {
  props: ['toggle', 'startingDate', 'price'],
  data() {
    return {
      dateFormatOptions: {
        year: 'numeric',
        month: 'numeric',
        day: 'numeric',
      },
      form: {
        note: 'note',
      },
    };
  },
  computed: {
    toggleModel: {
      get: function () {
        return this.toggle;
      },
      set: function () {
        this.$emit('closeSendOfferModel');
      },
    },
    disabled() {
      if (this.form.price && this.form.move_in_date && this.form.note)
        return false;
      return true;
    },
  },
  methods: {
    changeDate(value) {
      this.form.move_in_date = value;
    },
  },
  mounted() {
    if (this.price)
      this.form = { ...this.form, ...{ price: Number(this.price) } };
    if (this.startingDate)
      this.form = {
        ...this.form,
        ...{ move_in_date: new Date(this.startingDate) },
      };
  },
};
</script>

<style lang="scss">
header {
  &#modal-center___BV_modal_header_ {
    &.modal-header {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      font-weight: bold;
      padding: 32px 16px;

      h3 {
        color: black;
        font-size: 20px;
        margin-top: 8px;
      }
    }
  }
}
div {
  &#modal-center___BV_modal_content_ {
    &.modal-content {
      border-radius: 12px;
    }
  }
}
div {
  &#modal-center___BV_modal_body_ {
    &.modal-body {
      .duration {
        display: flex;
        flex-direction: column;
        margin-top: 24px;

        .duration-types {
          display: flex;
          justify-content: space-between;
        }
      }

      .duration-type {
        background-color: transparent;
        border: 1px solid #111111;
        border-radius: 8px;
        font-weight: 600;
        padding: 7px 12px;
        display: flex;
        gap: 4px;
        span {
          font-weight: 600;
          font-size: 12px;
          line-height: 12px;
          color: #111111;
          margin: auto;
        }
        .circle-container {
          width: 24px;
          height: 24px;
          border-radius: 50%;
          border: 1px solid black;
          display: flex;
          flex-direction: column;
          justify-content: center;
          text-align: center;
        }
        &.primary {
          .inner-circle {
            width: 14px;
            height: 14px;
            border-radius: 50%;
            background-color: $primary;
            margin: auto;
          }
        }
      }
      .duration-indicator {
        font-weight: 600;
        font-size: 16px;
        line-height: 16px;
        color: #111111;
        margin: 24px 0 16px;
      }
      .inner {
        display: flex;
        justify-content: space-between;
        overflow-x: scroll;
        padding-bottom: 20px;
        gap: 24px;
        span {
          width: 38px;
          height: 38px;
          padding: 10px 12px;
          font-weight: 700;
          font-size: 12px;
          text-align: center;
          border-radius: 50%;
          border: 1px solid #e3e5e5;
          color: #48535b;
          &.active {
            color: #0877f1;
            border-color: #0877f1;
          }
        }
      }
    }
  }
}

footer {
  &#modal-center___BV_modal_footer_ {
    &.modal-footer {
      .actions {
        display: flex;
        width: 100%;

        .btn {
          padding-top: 12px;
          padding-bottom: 12px;
          border-radius: 12px;
          font-weight: 600;
          border: none;
        }

        .btn-secondary {
          background-color: #f7f9fa;
          color: #111111;
          flex: 1;
          margin-right: 14px;
        }

        .btn-primary {
          background-color: #0078ff;
          flex: 3;
        }
      }
    }
  }
}
</style>
