<template>
  <b-row :class="['single-prop-card', 'm-2', 'my-3', shadow ? 'shadow' : '']">
    <!-- <b-col cols="12" class="px-0">
      <div class="tag_avatar d-flex align-items-center justify-content-center flex-column">
        <img width="95px" height="95px" src="@/assets/images/home/request_img.svg" alt="Request Card">
        <h3 class="mt-2 font-weight-bold">{{ dir === 'ltr' ? '#Long Stay' : isSliderStyle ? 'طويل المدى#' : '#طويل المدى' }}</h3>
        <div :class="['avatar', dir === 'ltr' ? 'avatar_en' : 'avatar_ar']">
          <img src="@/assets/images/home/avatar.png" alt="Avatar">
        </div>
      </div>
    </b-col> -->
    <b-col cols="12" class="px-0">
      <div class="request_card text-center">
        <div class="tag_category py-2">#Long_Stay</div>
        <div class="card_header d-flex align-items-center px-2">
          <div :class="['avatar', dir === 'ltr' ? 'mr-1' : 'ml-1']">
            <img width="50px" height="50px" src="@/assets/images/home/avatar.png" alt="Avatar">
          </div>
          <div class="name_reqPostedDate">
            <h2 class="text-body mb-0">{{ item.request.user.full_name }}</h2>
            <p class="mb-0">Request Posted {{ item.created_at }}</p>
          </div>
        </div>
        <hr class="my-3">
        
        <div class="card_body px-3">

          <div class="location_duration d-flex justify-content-between align-items-center mb-2">
            <div class="location d-flex">
              <img width="16px" height="16px" :class="dir === 'ltr' ? 'mr-1' : 'ml-1'" src="@/assets/images/home/MapPoint.svg" alt="Avatar">
              <!-- {{ Object.values(item.locations) }} -->
              <h4 class="text-body font-weight-bold"> {{ getFullLocation }}</h4>
            </div>
            <div class="duration d-flex align-items-center">
              <img width="16px" height="16px" :class="dir === 'ltr' ? 'mr-1' : 'ml-1'" src="@/assets/images/home/clock_white.svg" alt="Avatar">
              <p class="mb-0">{{ item.duration }}</p>
            </div>
          </div>

          <div class="amenities_required d-flex align-items-center mb-2">
            <div class="amenity bed d-flex align-items-center" v-if="item.num_of_bed_rooms">
              <img width="16px" height="16px" :class="dir === 'ltr' ? 'mr-1' : 'ml-1'" src="@/assets/images/home/Bed.svg" alt="Beds">
              <p>{{ item.num_of_bed_rooms }} Bedrooms</p>
            </div>
            <div class="amenity bath d-flex align-items-center" v-if="item.num_of_bath_rooms">
              <img width="16px" height="16px" :class="dir === 'ltr' ? 'mr-1' : 'ml-1'" src="@/assets/images/home/Bathtub.svg" alt="Bathrooms">
              <p>{{ item.num_of_bath_rooms }} Bath</p>
            </div>
          </div>

          <div class="property_types mb-1">
            <p class="text-body"> #{{ item.request.name }}</p>
          </div>

          <div class="budget d-flex align-items-center">
            <h5 class="text-body mb-0">Budget</h5>
            <p>
              <span class="font-weight-bold">~ {{ item.budget }} EGP</span>
              <span>/ {{ formating_price_type(item.request.term[0].toLowerCase()) }}</span>
            </p>
          </div>

          <div class="property d-flex justify-content-between align-items-center my-2">
            <div class="img_info d-flex align-items-center">
              <img width="35px" height="35px" src="@/assets/images/home/tenant-rating-landing.jpg" alt="Landlord Property">
              <h4>{{ item.property.name }}</h4>
            </div>
            <span class="font-weight-bold">Submitted</span>
          </div>

          <div :class="`chip ${offerStatus.toLowerCase()} w-100`">
            <div class="chip-label">
              <span>{{ offerStatus }}</span>
            </div>
          </div>
        </div>
      </div>
    </b-col>
    <!-- <b-col cols="12">
      <div>
        <h1 :class="['font-size-normal', 'font-weight-bold', 'dark-blue', isSliderStyle ? dir === 'ltr' ? 'text-left' : 'text-right' : null]" v-b-modal="'modal-' + item.id">
          {{ item.name }}
        </h1>
        <div :class="['d-flex', 'align-items-center', 'mt-3 mb-2', isSliderStyle ? dir === 'ltr' ? 'flex-row' : 'flex-row-reverse' : null]">
          <img width="14px" height="14px" :class="dir === 'ltr' ? 'mr-2' : 'ml-2'" src="@/assets/images/home/clock.svg" alt="Clock">
          <span :class="['font-size-small', 'light-gray', 'font-weight-semi-bold']">
            {{ item.duration + $t('common.from') }}
          </span>
          <span :class="['font-size-small', 'light-gray', 'font-weight-semi-bold']">
            {{ $dateFns.format(new Date(item.move_in_date), 'dd MMM yyyy') }}
          </span>
        </div>
        <span :class="['user_name', isSliderStyle ? dir === 'ltr' ? 'd-flex flex-row' : 'd-flex flex-row-reverse' : null]">
          <img width="15px" height="15px" :class="dir === 'ltr' ? 'mr-2' : 'ml-2'" src="@/assets/images/home/request_owner.svg" alt="Clock">
          {{ item.user_name }}
        </span>
      </div>
    </b-col> -->
    <!-- <b-col cols="12" class="my-2">
      <b-row :class="isSliderStyle ? dir === 'ltr' ? 'flex-row' : 'flex-row-reverse' : null">
        <b-col cols="7"></b-col>
        <b-col cols="5">
          <p :class="['gray', 'font-size-normal', 'mb-0', 'font-weight-bold', 'text-primary', 'd-flex align-items-center makegap flex-column']">
            <span class="font-size-small d-flex align-items-center price">{{ `${item.budget} ${$t('common.egp')}` }}</span>
            <span class="per_duration text-dark">{{ `${$t('common.le')} / ${formating_price_type(item.term[0].toLowerCase())}` }}</span>
          </p>
        </b-col>
      </b-row>
    </b-col> -->
  </b-row>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
    isHome:{
      type: Boolean,
      default: false
    },
    shadow:{
      type: Boolean,
      default: false
    },
    isSliderStyle: {
      type: Boolean,
      default: false
    }
  },
  data(){
    return {
      tag: 'Residential'
    }
  },
  methods: {
    formating_price_type(item){
      switch (item){
        case 'dy':
          return this.$t('form.postingRequest.stayingDetails.days')
        case 'my':
          return this.$t('form.postingRequest.stayingDetails.months')
        case 'yy':
          return this.$t('form.postingRequest.stayingDetails.years')
        case 'hr':
          return this.$t('form.postingRequest.stayingDetails.hours')
        case 'wy':
          return this.$t('form.postingRequest.stayingDetails.weeks')
        default:
          return false  
      }
    },
  },
  computed: {
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    },
    getFullLocation(){
      let location = this.item.request.locations;
      // console.log(location);
      var fullLocation = '';
      let lang = this.dir === 'ltr' ? 'en' : 'ar';
      fullLocation +=  `${location.city[lang]}${location.district ? `, ${location.district[lang]}` : ''}${location.neighborhood ? `, ${location.neighborhood[lang]}` : ''}` 
      return fullLocation;
    },
    offerStatus() {
      if(this.item.status){
        switch(this.item.status){
          case '0':
          case 'Rejected':
            return 'Rejected';
          case '1':
          case 'Accepted':
            return 'Accepted';
          case '2':
          case 'Counter':
            return 'Counter';
          case '3':
          case 'init':
            return 'Pending';
          default:
            return '';      
        }
      }
    },
  },
  mounted(){
    // console.log(this.getFullLocation);
    console.log(this.item);
  }
};
</script>

<style lang="scss" scoped>
.request_card {
  padding: 30px 0 0;
  background-color: #FFF;
  border-radius: 1rem;
  .card_header {
    .name_reqPostedDate {
      & > p {
        font-size: 14px !important;
        color: #5A5A5A !important;
      }
    }
  }
  .card_body {

    .location_duration {
      .location {
        h4 {
          font-size: 14px !important;
          font-weight: 600 !important;
        }
      }
    }
    .amenities_required {
      gap: 20px;
      .amenity {
        gap: 5px;
        p {
          font-size: 12px !important;
        }
      }
    }

    .property_types {
      p {
        font-weight: 500;
        font-size: 12px !important;
      }
    }

    .budget {
      gap: 10px;
      h5 {
        font-weight: 600;
        font-size: 12px !important;
      }
      p {
        span:first-of-type {
          font-size: 14px;
          color: #0078FF;
        }
        span:last-of-type {
          font-size: 10px;
        }
      }
    }

    .property {
      border:1px solid #5A5A5A;
      border-radius: 10px;
      padding: 10px;
      .img_info {
        gap: 5px;
        img {
          border-radius: 5px;
        }
        h4 {
          font-size: 12px;
          font-weight: bold;
          color: #121212;
        }
      }
      span {
        font-size: 10px !important;
        color: #0078FF;
      }
    }

    .chip {
      height: 30px;
      margin-top: 10px;
      left: 0;
      top: 0;
      box-sizing: border-box;
      cursor: pointer;
      display: inline-block;
      padding: 8px 12px;
      border-radius: 5px;

      .chip-label {
        font-weight: 600;
        font-size: 12px;
        line-height: 20px;
        margin: auto;
        vertical-align: middle;
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        text-align: center;
        text-transform: capitalize;
      }

      &.accepted {
        background-color: #def2e8;

        .chip-label {
          color: #00af48;
        }
      }

      &.rejected,
      &.cancelled {
        background-color: #ffeeee;

        .chip-label {
          color: #ff5252;
        }
      }

      &.pending,
      &.negotiating {
        background-color: #fff9ea;

        .chip-label {
          color: #ffc025;
        }
      }
    }

  }
  .tag_category {
    position: absolute;
    right: 0;
    top:0;
    transform: rotate(45deg) translateX(94px) translateY(-72px);
    background-color: #0078FF;
    color: #FFF;
    padding: 10px;
    width: 300px;
    text-align:center;
    font-weight: 600;
    font-size: 11px !important;
  }

}
// .row .col-12:first-of-type {
//   margin-bottom: 35px;
// }
.font-size-small {
  font-size: 11px !important;
  font-weight: bold;
  &.price {
    font-size: 16px !important;
  }
}
.tags {
  gap: 10px;
}
.single-prop-card {
  min-height: 258px !important;
  min-width: 285px !important;
  // max-width: 340px !important;
  background: white;
  border: 1px solid #e3e5e5;
  padding-bottom: 10px;
  border-radius: 1rem;
  position: relative;
  overflow: hidden;
  @media(max-width: 767px){
    min-width: 340px;
  }
  // max-width: 285px !important;
  cursor: pointer;
  .tag_avatar {
    position: relative;
    background-color: #D4DEE9 !important;
    padding: 45px 0;
    h3 {
      color: #000 !important;
    }
    .avatar {
      // position: absolute;
      // bottom: 0;
      // transform: translateY(50%);
      padding: 6px;
      background-color: #FFF;
      border-radius: 50%;
      // &.avatar_en {
      //   right: 2px;
      //   left: auto;
      // }
      // &.avatar_ar {
      //   left: 3px;
      //   right: auto;
      // }
    }
  }
  h1 {
    font-size: 16px !important;
  }
  .user_name {
    font-size: 12px !important;
    font-weight: 500 !important;
  }

  .per_duration {
    font-size: 12px !important;
  }
  // .isHome {
  //   font-size: 12px !important;
  // }
  .makegap {
    gap: 5px !important;
  }
}
</style>
