<template>
  <div class="offer-card">
    <div class="card-heading">
      <!-- Request Title  -->
      <div class="card-title">
        <h3>{{ item.name }}</h3>
        <span v-if="item.from_user">@ {{ item.from_user.full_name }}</span>
      </div>
      <div class="card-budget">
        <p class="font-size-normal mb-0 font-weight-bold">
          <span>{{ item.budget }} </span>
          <span class="font-size-small">{{ $t('common.egp') }}</span>
        </p>
        <span class="font-size-smaller light-gray">
          {{
            item.term === 1 ? $t('common.per_month') : $t('common.per_night')
          }}
        </span>
      </div>
    </div>

    <div class="d-flex my-3 justify-content-start">
      <div>
        <svg-icon name="calendar" height="24" width="24" />
        <span class="font-size-small light-gray font-weight-bold">
          {{ item.duration + $t('common.from') }}
          {{ $dateFns.format(new Date(item.move_in_date), 'dd MMM YYY') }}
        </span>
      </div>
    </div>

    <!-- NOTE: REPLACE WITH PROPERTY DATA -->
    <div class="property-info" v-if="item.property">
      <img
        class="property-image"
        :src="item.property.images[0]"
        width="52"
        height="52" />
      <div class="property-name">
        {{ item.property.property_name }}
      </div>
    </div>

    <hr />
    <div class="card-actions">
      <div :class="`chip ${offerStatus}`">
        <div class="chip-label">
          <span>{{ offerStatus }}</span>
        </div>
      </div>
      <template v-if="offerStatus === 'pending'">
        <div class="respond-button" @click="sendOfferModal = !sendOfferModal">
          <span>Respond</span>
          <img
            width="14px"
            height="14px"
            class="arrow-icon"
            :src="require('@/assets/images/sidebar/CaretRight.svg')"
            alt="photo" />
        </div>
      </template>
    </div>

    <InitOfferModal
      :toggle="sendOfferModal"
      :price="item.budget"
      :startingDate="item.move_in_date"
      @closeSendOfferModel="sendOfferModal = false"
      @sendOffer="initSendOffer" />

    <NegotiationModel
      :toggle="negotiationModel"
      @closeNegotiationModel="negotiationModel = false"
      @sendCounterOffer="sendCounterOffer" />
  </div>
</template>

<script>
import InitOfferModal from '@/components/dashboard/InitOfferModal';
import RejectionReasonsModel from '@/components/dashboard/RejectionReasonsModel';
import NegotiationModel from '@/components/dashboard/NegotiationModel';
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  components: {
    InitOfferModal,
    RejectionReasonsModel,
    NegotiationModel,
  },
  data() {
    return {
      sendOfferModal: false,
      rejectionModel: false,
      negotiationModel: false,
    };
  },
  computed: {
    offerStatus() {
      if (this.item.status) return this.item.status.toLowerCase();
      return '';
    },
  },
  methods: {
    async initSendOffer({ price, duration, move_in_date, note }) {
      try {
        await this.$axios.$post(`/user/requests/${this.item.id}/send-offer`, {
          price,
          property_id: this.item.property.id,
          duration,
          move_in_date,
          note,
        });

        this.$bvToast.toast(`Offer sent successfully`, {
          autoHideDelay: 3000,
          appendToast: false,
          variant: 'success',
        });

        this.sendOfferModal = false;
      } catch (error) {
        const message = error.response.data.Data
          ? error.response.data.Data.message
          : error.response.data.Message;

        this.$bvToast.toast(`Error occurred ${message}`, {
          autoHideDelay: 3000,
          appendToast: false,
          variant: 'danger',
        });
      }
    },
    async sendCounterOffer({ price, move_in_date, note }) {
      try {
        await this.$axios.$post(`/user/offers/${this.item.id}/interaction`, {
          type: 'counterOffer',
          price,
          move_in_date,
          note,
        });

        this.$bvToast.toast(`Counter offer sent successfully`, {
          autoHideDelay: 3000,
          appendToast: false,
          variant: 'success',
        });

        this.negotiationModel = false;
      } catch (error) {
        const message = error.response.data.Data
          ? error.response.data.Data.message
          : error.response.data.Message;

        this.$bvToast.toast(`Error occurred ${message}`, {
          autoHideDelay: 3000,
          appendToast: false,
          variant: 'danger',
        });
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.offer-card {
  background: white;
  border: 1px solid #e3e5e5;
  border-radius: 16px;
  padding: 24px;
  margin: 16px 0;

  .card-heading {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .card-title {
      margin: 0;
      h3 {
        font-size: 16px;
        font-weight: bold;
        color: black;
        margin: 0;
      }
      span {
        font-size: 13px;
        color: #48535b;
      }
    }

    .card-budget {
      text-align: center;

      p {
        color: #0078ff;
        text-align: center;
      }
    }
  }

  .property-info {
    padding: 12px 16px;
    border: 1px solid #e3e5e5;
    border-radius: 16px;

    .property-image {
      border-radius: 16px;
    }

    .property-name {
      display: inline-block;
      color: black;
      font-weight: bold;
      font-size: 16px;
      margin: 0 8px;
    }
  }

  .card-actions {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .chip {
      height: 36px;
      left: 0;
      top: 0;
      box-sizing: border-box;
      display: inline-block;
      padding: 8px 12px;
      border-radius: 100px;

      .chip-label {
        font-weight: 600;
        font-size: 12px;
        line-height: 20px;
        margin: auto;
        vertical-align: middle;
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        text-align: center;
        text-transform: capitalize;
      }

      &.accepted {
        background-color: #def2e8;

        .chip-label {
          color: #00af48;
        }
      }

      &.rejected,
      &.cancelled {
        background-color: #ffeeee;

        .chip-label {
          color: #ff5252;
        }
      }

      &.pending,
      &.negotiating {
        background-color: #fff9ea;

        .chip-label {
          color: #ffc025;
        }
      }
    }

    .respond-button {
      cursor: pointer;

      span {
        margin-right: 12px;
        line-height: 1;
      }
    }
  }
}
</style>
